import AddressBar from '../../Components/AddressBar';
import Button from '../../Components/Button';
import Menu from '../../Components/Menu';
import styled from '../../typed-components';
import React from 'react';
import { MutationFn } from 'react-apollo';
import Helmet from 'react-helmet';
import Sidebar from 'react-sidebar';
import RidePopUp from 'src/Components/RidePopUp';
import { getRides, userProfile } from 'src/types/api';
const Container = styled.div``;

const ExtendedButton = styled(Button)`
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 10;
  height: auto;
  width: 80%;
`;

const RequestdButton = ExtendedButton.extend`
  bottom: 100px;
`;

const MenuButton = styled.button`
  appearance: none;
  padding: 10px;
  position: absolute;
  top: 10px;
  left: 10px;
  text-align: center;
  font-weight: 800;
  border: 0;
  cursor: pointer;
  font-size: 20px;
  transform: rotate(90deg);
  z-index: 2;
  background-color: transparent;
`;

const Map = styled.div`
  position: absolute;
  height: 100%;
  width: 100%;
`;

interface IProps {
  isMenuOpen: boolean;
  toggleMenu: () => void;
  loading: boolean;
  mapRef: any;
  toAddress: string;
  onAddressSubmit: () => void;
  onInputChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  price?: number;
  data?: userProfile;
  requestRideFn?: MutationFn;
  nearbyRide?: getRides;
  acceptRideFn?: MutationFn;
  nearbyRideSubscription: () => void;
}

class HomePresenter extends React.Component<IProps> {
  public componentDidMount() {
    this.props.nearbyRideSubscription();
  }
  public render() {
    const {
      isMenuOpen,
      toggleMenu,
      loading,
      toAddress,
      mapRef,
      onInputChange,
      onAddressSubmit,
      price,
      data: { GetMyProfile: { user = null } = {} } = {},
      requestRideFn,
      nearbyRide: { GetNearbyRide: { ride = null } = {} } = {},
      acceptRideFn
    } = this.props;
    return (
      <Container>
        <Helmet>
          <title>Home | UberClone</title>
        </Helmet>
        <Sidebar
          sidebar={<Menu />}
          open={isMenuOpen}
          onSetOpen={toggleMenu}
          styles={{
            sidebar: {
              width: "80%",
              background: "white",
              zIndex: "10"
            }
          }}
        >
          {!loading && (
            <MenuButton onClick={() => toggleMenu()}>|||</MenuButton>
          )}
          {user && !user.isDriving && (
            <React.Fragment>
              <AddressBar
                name={"toAddress"}
                onChange={onInputChange}
                value={toAddress}
                onBlur={null}
              />
              <ExtendedButton
                onClick={onAddressSubmit}
                disabled={toAddress === ""}
                value={price ? "Change address" : "Pick Address"}
              />
            </React.Fragment>
          )}
          {price && (
            <RequestdButton
              onClick={requestRideFn}
              disabled={toAddress === ""}
              value={`Request Ride ($${price})`}
            />
          )}
          {ride && (
            <RidePopUp
              id={ride.id}
              pickUpAddress={ride.pickUpAddress}
              dropOffAddress={ride.dropOffAddress}
              price={ride.price}
              distance={ride.distance}
              passengerName={ride.passenger.fullName!}
              passengerPhoto={ride.passenger.profilePhoto!}
              acceptRideFn={acceptRideFn}
            />
          )}
          <Map innerRef={mapRef} />
        </Sidebar>
      </Container>
    );
  }
}

export default HomePresenter;



