import Form from '../../Components/Form';
import Header from '../../Components/Header';
import Input from '../../Components/Input';
import Message from '../../Components/Message';
import styled from '../../typed-components';
import { getChat, userProfile } from '../../types/api';
import React from 'react';

const Container = styled.div``;
interface IProps {
  data?: getChat;
  userData?: userProfile;
  loading: boolean;
  messageText: string;
  onSubmit: () => void;
  onInputChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  subscribeToRideStatus: () => void;
}

const Chat = styled.div`
  height: 80vh;
  overflow: scroll;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const InputCont = styled.div`
  padding: 0 20px;
`;

class ChatPresenter extends React.Component<IProps, any> {
  public componentDidMount() {
    this.props.subscribeToRideStatus();
  }
  public render() {
    const {
      loading,
      data: { GetChat: { chat = null } = {} } = {},
      userData: { GetMyProfile: { user = null } = {} } = {},
      messageText,
      onInputChange,
      onSubmit
    } = this.props;
    return (
      <Container>
        <Header title={"Chat"} />
        {!loading && chat && user && (
          <React.Fragment>
            <Chat>
              {chat.messages &&
                chat.messages.map(message => {
                  if (message) {
                    return (
                      <Message
                        key={message.id}
                        text={message.text}
                        mine={user.id === message.userId}
                      />
                    );
                  }
                  return null;
                })}
            </Chat>
            <InputCont>
              <Form submitFn={onSubmit}>
                <Input
                  value={messageText}
                  placeholder={"Type your message"}
                  onChange={onInputChange}
                  name={"message"}
                />
              </Form>
            </InputCont>
          </React.Fragment>
        )}
      </Container>
    );
  }
}
export default ChatPresenter;



